<template>
  <div id="apps">
        <van-nav-bar
  class="init-header"
  title="编辑资料"
  left-arrow
  @click-left="onClickLeft"
>
</van-nav-bar>
 <van-cell-group>
  <van-cell is-link @click="onSelectchange" >
      <span>头像</span>
<img :src="form.url" name="adapter"  class="initsimg"/>
  </van-cell>
  <van-cell is-link  @click="goEditName">
      <span>昵称</span>
      <span style="float: right;">{{form.name}}</span>
  </van-cell>
</van-cell-group>

<van-actionsheet 
  v-model="show"
  @select="onSelect"
>
<p calss="pinit">
<van-uploader :after-read="onRead" accept="image/*">
  相册
</van-uploader>
</p>
<p calss="pinit" style="text-align: center;border-top: 1px solid #999">
<van-uploader :after-read="onRead" accept="image/*" capture="camera">
  拍照
</van-uploader></p>
<p style="background-color:rgba(0,0,0,.7);height: 14px;"></p>
<p @click="onSelectchange" calss="pinit" style="text-align: center;color: red;">取消</p>
</van-actionsheet>
  </div>

</template>

<script>
import { Actionsheet } from 'vant';
import { Uploader } from 'vant';
import {Toast} from 'vant'
import { login } from "../../src/api/login";
export default {
  name: "login",
  components: {},
  data() {
    return {
       show: false,
        form:{
          name:'132',
          url:'../../static/images/icon/icon_9.png'
        },
      actions: [
        {
          name: '拍照'
        },
        {
          name: '本地相册',
        } 
      ]
    };
  },
  computed: {
  },
  methods: {
   onClickLeft() {
      this.$router.back(-1);
    },
    onSelectchange(){
        this.show = !this.show
    },
    onRead(file) {
      let fd= new FormData();
        fd.append("upfile", file.file);//第一个参数字符串可以填任意命名，第二个根据对象属性来找到file
                
        axios.post(url, {params:fd}) //url是服务器的提交地址
            //成功回调
            .then(res => {this.form.url = res.data.imgpath }) //将服务器返回的图片链接添加进img数组，进行预览展示
              //失败回调
            .catch(err => {alert(err);});
    },
    goEditName(){
      this.$router.push({ path:'/editname',query:{ name:this.form.name}})  
    },
     onSelect(item) {
      // 点击选项时默认不会关闭菜单，可以手动关闭
      this.show = false;
      Toast(item.name);
    }
  },
  watch: {},
  directives: {},
  beforeCreate() {},
  created() {}
};
</script>

<style lang="less" scoped>
@import url("../assets/css/login.less");
</style>
<style scoped>
.initsimg{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    float: right;
}
#apps >>> .van-cell{
line-height: 50px
}
#apps >>> .van-cell__right-icon{
    line-height: 50px;
}
#apps >>> .van-actionsheet__cancel, .van-actionsheet__item{
color: red;
}
#apps >>> .van-actionsheet{
    width: 92%;
    margin-left: 4%;
    margin-bottom: 12px;
}
#apps >>> .van-nav-bar .van-icon{
    color: #2c3e50
}
.pinit{
  width: 100%;
  padding:0;
  margin:0
}
#apps >>> .van-uploader{
  width: 100%;
  text-align: center;
      line-height: 35px;
}
#apps >>> p{
  margin:0;
  line-height: 50px;
}
</style>




