<template>
  <div id="apps">
        <van-nav-bar
  class="init-header"
  title="称呼"
  right-text="保存"
  @click-right="onClickRight"
  left-arrow
  @click-left="onClickLeft"
>
</van-nav-bar>
 <van-cell-group>
<van-cell-group>
  <van-field v-model="name" placeholder="请输入用户名"  clearable/>
</van-cell-group>
</van-cell-group>
  </div>

</template>

<script>
import { Field } from 'vant';
import {Toast} from 'vant'
import { login } from "../../src/api/login";
export default {
  data() {
    return {
    name:''
    };
  },
  mounted () {
    this.name =  this.$route.query.name; 
  },
  methods: {
   onClickLeft() {
      this.$router.back(-1);
    },
    onClickRight(){
      let para ={
          name:this.name
      }
      this.onClickLeft()
    },
    onSelectchange(){
        this.show = !this.show
    },
     onSelect(item) {
      // 点击选项时默认不会关闭菜单，可以手动关闭
      this.show = false;
      Toast(item.name);
    }
  },
};
</script>

<style lang="less" scoped>
@import url("../assets/css/login.less");
</style>
<style scoped>
.initsimg{
    border-radius: 50%;
    width: 50px;
    height: 50px;
    float: right;
}
#apps >>> .van-cell{
line-height: 50px
}
#apps >>> .van-cell__right-icon{
    line-height: 50px;
}
#apps >>> .van-actionsheet__cancel, .van-actionsheet__item{
color: red;
}
#apps >>> .van-actionsheet{
    width: 92%;
    margin-left: 4%;
    margin-bottom: 3%
}
#apps >>> .van-nav-bar__text{
    color: red
}
#apps >>> .van-nav-bar .van-icon{
    color: #2c3e50
}
</style>




